import axios from 'axios';
import { FETCH_PRODUCTS,FETCH_USER,FETCH_ORDER,RESET, UPDATE_ORDER, FETCH_CURRENCY, FETCH_PAYMENT, FETCH_SURVEYS, UPDATE_PRODUCTS, UPDATE_CART, CHECK_USER, SUCCESS, FAILED, LOGOUT } from './types';
import * as actions from '../actions';


export const resetTask = ()=>dispatch => {
  dispatch({type: RESET, payload: {}})
}
export const fetchProducts = ()=> async dispatch => {
  const res = await axios.get('https://project-aqua.herokuapp.com/data');
 // console.log(res);
  dispatch({type: FETCH_PRODUCTS, payload: res.data});
};

export const fetchCurrency = ()=>async dispatch=>{
  const res = await axios.get('https://free.currconv.com/api/v7/convert?q=USD_INR&compact=ultra&apiKey=e12828365b5fa0c320e6');
  dispatch({type: FETCH_CURRENCY, payload: res.data});
}

export const fetchOrders = (user)=>async dispatch=>{
  console.log(user)
  const res = await axios.post('https://project-aqua.herokuapp.com/order', {user: user});
  console.log(res.data)
  dispatch({type: FETCH_ORDER, payload: res.data});
}

export const updateProducts = (products)=> dispatch => {
  dispatch({type: UPDATE_PRODUCTS, payload: products})
}

export const updateOrder = (order)=> dispatch => {
  dispatch({type: UPDATE_ORDER, payload: order})
}

export const updateCart = (products)=> dispatch => {

  let cartItems = products;
  let sortedCart = [];
  let totalAmount = 0;
  cartItems.map((data, pos)=>{
    if(data.quantity>0){
      totalAmount += data.totalPrice;
      sortedCart.push(data);
    }
        
});
let cart = {
  cart: sortedCart,
  totalPrice: totalAmount
}
  dispatch({type: UPDATE_CART, payload: cart})
}


export const logOut = ()=> dispatch => {
  dispatch({type: LOGOUT});
}
export const test = (token, charge, cart, price, order, userId)=> async dispatch => {
  var today = new Date();
  var dd = String(today.getDate()).padStart(2, '0');
  var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
  var yyyy = today.getFullYear();
  
  today = dd + '/' + mm + '/' + yyyy;

  var d = new Date();
  let h = d.getHours();
  let m = d.getMinutes();
  var ampm = h >= 12 ? 'pm' : 'am';
  h = h % 12;
  h = h ? h : 12;
  m = m < 10 ? '0'+m : m;
  var time = h + ':' + m + ' ' + ampm;

  var customer = {
    name: token.card.name,
    address: {
      line1: token.card.address_line1,
      postal_code: token.card.address_zip,
      city: token.card.address_city,
      state: token.card.address_state,
      country: token.card.address_country,
    }
  };
  charge.source = token.id;
  charge.shipping = customer;
  console.log(cart);
let obj = {
  cart: cart,
  price: price,
  charge: charge,
  date: today,
  time: time,
  order: order,
  user: userId
};
  try{
    const res = await axios.post('https://project-aqua.herokuapp.com/test', obj);
    if(res.data.info === 'success'){
      console.log(res.data.msg);
      dispatch({type: SUCCESS , payload: res.data.msg})
      
    } else{
      console.log(res.data.msg);
      
     
    
    }
  } catch(e){
    console.log('failed');
    console.log(e);
    dispatch({type: FAILED , payload: e})
  }
 
}
