import React, { useState, useEffect } from "react";
import Navigate from "./Navigate";
import { connect } from 'react-redux';
import * as actions from '../../actions';
import '../../res/main.scss';
import jsPDF from 'jspdf'

function Order(props){
  
useEffect(()=>{
    console.log(props.user._id)
    props.fetchOrders(props.user._id)
},[])


    
    let showRecieptModal = (oreder)=>{
        
        
            let cart = oreder.cart;
            // Example From https://parall.ax/products/jspdf
            var doc = new jsPDF();
            doc.setFontSize(22);

            doc.text("Project Aqua", 105, 20, null, null, "center");
            doc.setFontSize(16);
                

doc.text("Order Details", 105, 30, null, null, "center");
doc.setFontSize(8);
doc.text(`Date: ${oreder.date}`, 15, 40, null, null);
doc.text(`Time: ${oreder.time}`, 15, 50, null, null);

doc.setFontSize(16);
doc.text("Delivery Details", 105, 60, null, null, "center");
doc.setFontSize(8);
doc.text(`Name: ${oreder.time}`, 15, 70, null, null);
doc.text(`Contact Number: ${oreder.time}`, 15, 80, null, null);
doc.text(`Delivery Date: ${oreder.time}`, 15, 90, null, null);
doc.text(`Time: ${oreder.time}  -  ${oreder.time}`, 15, 100, null, null);
doc.text(`Delivery Address: `, 15, 110, null, null);
doc.text(`${oreder.time}`, 25, 120, null, null, "center");





            doc.setLineWidth(1);
            doc.setDrawColor(0);
            if(oreder.status === 'Pending'){
                doc.setFontSize(8);
                doc.text("Pending", 200, 20, null, null, "center");
                doc.setFillColor(255,255,153);

            } else if(oreder.status === 'Delivered'){
                doc.setFontSize(8);
                doc.text("Delivered", 200, 20, null, null, "center");
                doc.setFillColor(153,255,153);

            } else {
                doc.setFontSize(8);
                doc.text("Canceled", 200, 20, null, null, "center");
                doc.setFillColor(255,153,153);

            }
            doc.circle(200, 10, 5, "FD");

       ////// table
       let generateTable = (cartItems)=>{
       var result = [];
        cartItems.map((cart, pos)=>{
            let data = {
                id: (pos+1).toString(),
                Product: cart.name,
                Type: cart.type,
                Price: `${cart.price.toString()}`,
                Quantity: cart.quantity.toString(),
                Total_Price: cart.totalPrice.toString()
              };
              result.push(Object.assign({}, data));
        })
    
    

      console.log(result)
      return result;
    };
    let headers = createHeaders([
        "Product",
        "Type",
        "Price",
        "Quantity",
        "Total_Price"
      ]);
      console.log(headers);
      function createHeaders(keys) {
        var result = [];
        for (var i = 0; i < keys.length; i += 1) {
          result.push({
            id: keys[i],
            name: keys[i],
            prompt: keys[i],
            width: 55,
            align: "center",
            padding: 0
          });
        }
        console.log(result)
        return result;
      }
      doc.table(1, 140, generateTable(cart), headers);

            // Save the Data
            doc.output('dataurlnewwindow');

            //doc.save(`Generated.pdf`);
        
    }

    let generateTableRows = ()=>{
        try{
        return props.orderList.reverse().map((data, position)=>{
            return(
                <tr class={`tab-row row-${position}`}>
            <td>{data.date}</td>
            <td>{data.time}</td>
            <td>₹{data.price}</td>
            <td>{data.status}</td>
            <td><a onClick={()=>showRecieptModal(data)} class="waves-effect blue text-white waves-light btn btn-small">View</a>
        </td>
          </tr>
            );
        });
    }  catch(e){

        return(
            <h1></h1>
        );
    }
}
    return(
        
           <div> 
           <Navigate />
           <div style={{ backgroundColor: "#fff" }} class="container mt-5 mb-5">
           <table class="responsive-table centered">
        <thead>
          <tr>
             
              <th class="center">Date</th>
              <th class="center">Time</th>
              <th class="center">Order Amount</th>
              <th class="center">Status</th>
              <th class="center">Receipt</th>

          </tr>
        </thead>

        <tbody>
            {generateTableRows()}
        </tbody>
      </table>
      </div>
      </div> 
           
    );
    
}
function mapStateToProps(myState){
    console.log(myState.authReducer);
    return {
      orderList: myState.authReducer.orderList,
      user: myState.authReducer.user
  
  };        //// see consolelog and fincd the crct obj
  }
export default connect(mapStateToProps, actions)(Order);