import React, { useState, useEffect, useRef } from "react";
import Navigate from "./Navigate";
import TextField from "@material-ui/core/TextField";
import { makeStyles } from '@material-ui/core/styles';
import $ from 'jquery';
import { useHistory } from 'react-router-dom';
import M from 'materialize-css'
import StripeCheckout from 'react-stripe-checkout'
import { connect } from 'react-redux';
import * as actions from '../../actions';
import '../../res/main.scss';

function Checkout(props){
  const [isFilled, setFilled] = useState(false);
    let dateInstances = null;
    let startInstances = null;
    let endInstances = null;
    let history = useHistory();
    const nameRef = useRef();
    const addressRef = useRef();
    const contactRef = useRef();
    const dateRef = useRef();
    const startRef = useRef();
    const endRef = useRef();

    


    const mounted = useRef();
    let routeChange = (path)=>{
      history.push(path);
    // props.fetchProducts();

};



      let launchPayment = ()=>{
        if(nameRef.current.value.trim() === '' || contactRef.current.value.trim() === '' || addressRef.current.value.trim() === '' || dateRef.current.value.trim() === '' || startRef.current.value.trim() === '' || endRef.current.value.trim() === ''){
          M.toast({html: 'Please Fill All the fields!'});
         
        } else{
          let orderInfo = {
            name: nameRef.current.value.trim(),
            contact: contactRef.current.value.trim(),
            address: addressRef.current.value.trim(),
            date: dateRef.current.value.trim(),
            startTime: startRef.current.value.trim(),
            endTime: endRef.current.value.trim()
          };
          props.updateOrder(orderInfo);
          routeChange('/payment');
        }
      }
   
      let launchDatePicker = (mode)=>{
          let options = {};
          let instance = null;
        if(mode === 'date'){
           
            instance = M.Datepicker.init(document.querySelector('.datepicker'), options);

        }
        else if(mode === 'start'){
            instance = M.Timepicker.init(document.querySelector('.starttime'), options);

        }
        else{
            instance = M.Timepicker.init(document.querySelector('.endtime'), options);

        }
       
         
        instance.open();
       
       
      }

   
    
    return(
        
           <div> 
           <Navigate />
           <div style={{ backgroundColor: "#fff" }} class="container mt-5 mb-5">
          
           <div class="row">
    <form class="col s12">
      <div class="row">
        <div class="input-field col s12">
          <input ref={nameRef} id="first_name" type="text" class="validate" />
          <label for="first_name">Name</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input ref={addressRef} id="password" type="password" class="validate" />
          <label for="password">Delivery Address</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input ref={contactRef} id="email" type="email" class="validate" />
          <label for="email">Contact Number</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
        <input ref={dateRef} onClick={()=>launchDatePicker('date')}  type="text" class="datepicker" />
          <label for="email">Delivery Date</label>
        </div>
      </div>
      <div class="row">
      <div class="input-field col s6">
        <input ref={startRef} onClick={()=>launchDatePicker('start')}  type="text" class="starttime" />
          <label for="email">From</label>
        </div>
        <div class="input-field col s6">
        <input ref={endRef} onClick={()=>launchDatePicker('end')}  type="text" class="endtime" />
          <label for="email">To</label>
        </div>
      </div>
      <div class="row">
        <h5>Choose Payment Options</h5>
      </div>

   
    <div class="row justify-content-center">


    </div>


      <div class="row ">
          <div onClick={()=> launchPayment()} style={{width:'100%'}} class="mt-3 mb-3 btn">Place Order</div>
      </div>
    </form>
  </div>
      </div>
      </div> 
           
    );
}

function mapStateToProps(myState){
  console.log(myState.authReducer);
  return {
    products: myState.authReducer.products,
    price: myState.authReducer.price,
    cart: myState.authReducer.cart
  };        //// see consolelog and fincd the crct obj
}


export default connect(mapStateToProps, actions)(Checkout);